module.exports = ".frame{\n    display: grid;\n    grid-template-columns: 1fr;\n    justify-items: center;\n}\n\n.buttons{\n    margin: 30px 10px;\n    display: flex;\n    flex-direction: row;\n    justify-items: center;\n    align-items: center;\n}\n\n.el{\n    padding: 0px 5px;\n}\n\n.loading{\n    font-size: 1.5rem;\n}\n\n.content{\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n}\n\n.all-notes{\n    padding: 0px 10px;\n    font-size: 1.5rem;\n}\n\n.current-note{\n    display: grid;\n    justify-items: center;\n    align-items: center;\n    padding: 0px 10px;\n    /* border: 1px solid gray; */\n    border-radius: 15px;\n}\n\n.resource{\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n    align-items: center;\n    /* border: 1px solid gray; */\n    border-radius: 15px;\n    margin: 5px 0px;\n    padding: 15px 5px;\n    justify-items: center;\n}\n\n.rid{\n    margin: 0px 3px;\n}\n\n.note{\n    border: 2px solid gray;\n    width: 500px;\n    min-height: 200px;\n    padding: 10px;\n    font-size: 1rem;\n    border-radius: 10px;\n}\n\n.share-inp{\n    width: 120px;\n}\n\n@media(max-width:768px){\n    .content{\n        grid-template-columns: 1fr;\n    }\n\n}\n\n@media(max-width:550px){\n    .note{\n        width: 280px;\n    }\n    \n}\n/*# sourceMappingURL=data:application/json;base64, */"